<template>
    <div>
        <div class="popup__content">
            <div class="h1 mb8 text-color-red text-center">Внимание!</div>

            <div class="mt16 text-center text-size-ms">
                <div>
                    Вы пытаетесь переместить раздел в другой, содержащий <strong v-html="count"></strong>&nbsp;<strong v-html="mfWord(count, ['акцию', 'акции', 'акций'])"></strong>.<br>
                    Для
                    <span v-if="count > 1">них</span>
                    <span v-else>неё</span>
                    в текущем разделе будет создан скрытый раздел, поэтому придумайте для него название:


                   <div class="mt32 text-left">
                        <input-styled v-model="sectionName"
                                    ref="section_name"
                                    :class="{error: sectionNameError}"
                                    :placeholder="'Название раздела'"
                                  ></input-styled>

                        <!-- старый вариант инпута -->
                        <!-- <input ref="section_name" type="text" v-model="sectionName" class="inp-s full size-s"
                           :class="{error: sectionNameError}"
                           placeholder="Название раздела"> -->
                    </div>
                </div>
            </div>


            <div class="mt32 text-center">
                <button class="btn mr24" @click="apply">Подтвердить</button>
            </div>
        </div>
    </div>
</template>

<script>
    import {mapState} from 'vuex'
    import InputStyled from '@/components/forms/InputStyled.vue'

    export default {
        name: "CategoriesMove",
        components: {
            InputStyled
        },
        props: {
            count: {default: 'несколько'}
        },
        data: function () {
            return {
                sectionName: '',
                sectionNameError: false
            }
        },
        created() {
        },
        mounted() {
            this.setFocus();
        },
        methods: {
            setFocus: function() {
                if (typeof (this.$refs['section_name']) !== 'undefined') {
                    this.$refs['section_name'].focus();
                }
            },
            apply: function () {
                if (this.sectionName) {
                    this.$emit('apply', {sectionName: this.sectionName});
                } else {
                    this.sectionNameError = true;
                    this.setFocus();
                }
            },
            cancel: function () {
                this.$emit('cancel');
            },
            mfWord: function (number, titles) {
                let cases = [2, 0, 1, 1, 1, 2];
                return titles[(number % 100 > 4 && number % 100 < 20) ? 2 : cases[(number % 10 < 5) ? number % 10 : 5]];
            }
        },
        computed: {
            ...mapState({
                popups: state => state.popups.items,
            }),
        }
    }
</script>

